.login{
    background-color: white;
    display: flex;
    height:100vh;
    flex-direction: column;
    align-items: center;
}

.login__container{
    width:300px;
    display: flex;
    flex-direction: column;
    padding:20px;
    height:fit-content;
    border: 1px solid lightgray;
}

.login__container > h1{
      font-weight: 700;
      margin-bottom: 20px;
}

.login__container > form > h5 {
     margin-bottom: 5px;
}

.login__container > form > input {
    height:30px;
    margin-bottom: 10px;
    background-color: white;
    width:98%;
    padding-left:5px;
}

.login__container > p {
    margin-top: 15px;
    font-size: 15px;
    margin-bottom: 10px;
}

.login__logo{
    margin: 20px 0 0 0;
    width:200px;
    object-fit: contain;    
}

.login__signinbuttton{
    background-color: #f0c14b;
    border-radius: 2px;
    width:100%;
    height:30px;
    border: 1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login__registerbutton{
    border-radius: 2px;
    width:100%;
    height:30px;
    border: 1px solid;
    margin-top:10px;
    border-color: darkgray;
}
