.cp{
    display: flex;
    margin-top:20px;
    margin-bottom: 20px;
    border-bottom: 1px solid;
    padding-bottom:5px;
}

.cp__image{
    object-fit: contain;
    width:180px;
    height:180px;
}

.cp__info{
    padding-left:20px;
    padding-top: 35px;
}

.cp__title{
    font-size:17px;
    font-weight:800;
}

.cp__info > button{
    background: #f0c14b;
    border: 1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111;
}